/* General styles */
body {
  margin: 0;
  font-family: Arial, sans-serif;
  color: #333;
  background-color: #f9f9f9;
}

.App {
  text-align: center;
}

/* Navbar styles */
.navbar {
  background-color: #333;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.nav-links {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.nav-links li {
  margin: 0 1rem;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
}

.nav-links a:hover {
  color: #ffd700;
}

/* Landing page styles */
.landing-page {
  padding: 4rem 2rem;
}

.headshot {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  border: 4px solid #333;
  margin-bottom: 1.5rem;
}

.name {
  font-size: 2.5rem;
  margin: 0;
  color: #333;
}

.greeting {
  font-size: 1.25rem;
  margin-top: 1rem;
  color: #666;
}